
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.8.1/font/bootstrap-icons.css");


/* CSS For header section */
.header-container {
  display: flex;
  padding-top:10px;
  padding-bottom: 10px;
  background-color: white;
  box-shadow: 0px 4px 14px rgba(24, 160, 251, 0.12);
  border-radius: 0 0 10px 10px;
  justify-content: space-between;
}
.flex-items {  
  margin: 10px;
  cursor: pointer;
  width: 30px; 
  height:30px; 
  text-align: center; 
  font-size: 1.5em;
} 
.right-header{
  display: flex;
  flex-direction: row-reverse; 
}





/* CSS for Main Page */
.aboutdiana{
  font-family: 'Reptile';
  margin-top: 20px;
  font-style: normal;
  font-weight: 500;
  font-size: 33px;
  text-align: center;
  color: #1B1B50;
}
.homepagediv{
  box-sizing: border-box;
  display: block;
}
.boldtitle1{
  margin: 30px 0 30px 0;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  /* identical to box height */

  text-align: center;

  color: #1B1B50;
}

.subcontent1{
  /* Diana empowers you to understand how your body works so you can look and feel your best. */
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  font-size: 14px;
  color: #000000;
  margin: 5px 10% 10px 10%;
  display: inline-block;
  width: 80%;
}
.sectiondivider{
  display: inline-block;
  margin: 15px 10% 10px 10%;
  width: 80%;
  border-bottom: 1px solid #000000;
}
.homepagediv{
  padding-bottom: 260px;
}
.boldtitle2{
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 33px;
  text-align: center;
  color: #1B1B50;
}
.boldtitle3{
  font-weight: 500;
  font-size: 33px;
  margin: 32px 10% 32px 10%;
  /* or 44px */
  font-family: 'Reptile';
  text-align: center;
  color: #1B1B50;
